body {
  margin: 0;
  font-family: philosopherregular, sans-serif, -apple-system, BlinkMacSystemFont,
    "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
  font-family: "philosopherregular";
  src: local("philosopherregular"),
    url(./assets/fonts/philosopher-regular-webfont.woff2) format("woff2");
  /* other formats include: 'woff2', 'truetype, 'opentype',
                            'embedded-opentype', and 'svg' */
}

@font-face {
  font-family: "philosopherbold";
  src: local("philosopherbold"),
    url(./assets/fonts/philosopher-bold-webfont.woff2) format("woff2");
}

@font-face {
  font-family: "pinyon_scriptregular";
  src: local("pinyon_scriptregular"),
    url(./assets/fonts/pinyonscript-regular-webfont.woff2) format("woff2");
}
